    "This version of node.js \"%s\" on \"%s\" is deprecated. Please install node.js 6, 8 or newer": {"en": "ioBroker supports the LTS versions 6, 8, 10 or newer of node.js. Please update your version (\"%s\") on host \"%s\" to one of the supported versions. We recommend to use Node.js 6.", "de": "ioBroker untersttzt die LTS Versionen 6, 8, 10 oder neuer von Node.js. Bitte aktualisieren Sie die verwendete Node.js Version (\"%s\") auf dem Rechner \"%s\" auf eine untersttzte Version. Wir empfehlen die Verwendung von Node.js 6.", "ru": "  node.js \"%s\"  \"%s\" .  node.js 6, 8, 10  .", "pt": "ioBroker suporta as verses LTS 6, 8 e 10 do Node.js. Atualize sua verso (\"%s\") no host \"%s\" para uma das verses suportadas. Recomendamos usar Node.js 6.", "nl": "ioBroker ondersteunt de LTS-versies 6, 8 en 10 van node.js. Update uw versie (\"%s\") op host \"%s\" naar een van de ondersteunde versies. We raden aan om node.js 6 te gebruiken.", "fr": "ioBroker supporte les versions LTS 6, 8 et 10 de node.js. Veuillez mettre  jour votre version (\"%s\") sur l'hte \"%s\" avec une des versions prises en charge. Nous vous recommandons d'utiliser node.js 6.", "it": "ioBroker supporta le versioni LTS 6, 8 e 10 di node.js. Aggiorna la tua versione (\"%s\") sull'host \"%s\" a una delle versioni supportate. Si consiglia di utilizzare node.js 6.", "es": "ioBroker soporta las versiones LTS 6, 8 y 10 de Node.js. Actualice su versin (\"%s\") en el host \"%s\" para una de las versiones soportadas. Recomendamos usar Node.js 6.", "pl": "ioBroker obsuguje wersje LTS 6, 8 i 10 pliku node.js. Zaktualizuj swoj wersj (\" %s\") na hocie \" %s\" do jednej z obsugiwanych wersji. Zalecamy korzystanie z Node.js 6."},
