    "diag-note": {                                   "en": "We worked hard to create this project. In return we expect from you some usage statistics.<br>Every time the adapter list is updated, the anonymized statistics are sent. We respect your privacy, so no private information will be transmitted.<br>Thank you!", "de": "Wir haben hart daran gearbeitet, dieses Projekt auf die Beine zu stellen. Als Gegenleistung bitten wir Sie, uns die Nutzungs-Statistik zu senden.<br>Es werden keine privaten Informationen zu ioBroker.org gesendet. Jedes Mal wenn die Adapterliste aktualisiert wird, wird auch die anonymisierte Statistik verschickt.<br>Vielen Dank!", "ru": "     .       ,  .<br>      ioBroker.net.  ,    ,    .<br> !", "pt": "Trabalhamos duro para criar este projeto. Em troca, pedimos que nos envie as estatsticas de uso. <br> Nenhuma informao privada ser enviada para ioBroker.net. Cada vez que a lista do adaptador  atualizada, as estatsticas annimas tambm sero enviadas. <br>Obrigado!", "nl": "We hebben hard gewerkt om dit project mogelijk te maken. Als equivalent verwachten we van u enkele statistieken over het gebruik. <br> Er zal geen persoonlijke informatie naar ioBroker.net worden verzonden. Telkens wanneer u op de update-adapterlijst klikt, worden de geanonimiseerde statistieken verzonden. <br> Dankjewel!", "fr": "Nous avons travaill dur pour crer ce projet. A charge de revanche, nous attendons de vous des statistiques sur l'utilisation.<br>Aucune information prive ne sera envoye  ioBroker.net. Chaque fois que vous appuyez sur Mettre  jour la liste des adaptateurs, les statistiques anonymises seront envoyes.<br>Merci!", "it": "Abbiamo lavorato sodo per creare questo progetto. In cambio ci aspettiamo da voi alcune statistiche di utilizzo. <br> Ogni volta che la lista dell'adattatore viene aggiornata, vengono inviate le statistiche anonime. Rispettiamo la tua privacy, quindi non verranno trasmesse informazioni private. <br> Grazie!", "es": "Trabajamos duro para crear este proyecto. En cambio, pedimos que nos enve las estadsticas de uso. <br>Ninguna informacin privada ser enviada a ioBroker.net. Cada vez que se actualiza la lista del adaptador, tambin se enviarn las estadsticas annimas. - <br>Gracias!", "pl": "Ciko pracowalimy, aby stworzy ten projekt. W zamian spodziewamy si pewnych statystyk uytkowania. <br> Za kadym razem, gdy lista adapterw jest aktualizowana, wysyane s anonimowe statystyki. Szanujemy twoj prywatno, wic adne prywatne informacje nie bd przekazywane. <br> Dzikuj!"},
