    "update-part1": {                                "en": "Because ioBroker runs on many very different platforms, only manual updating is possible at the moment. To start the manual update, please go to your controller via console and execute the following:", "de": "Aufgrund der Vielzahl an Hardware und Plattformen, auf denen ioBroker luft, muss der js-controller manuell aktualisiert werden. Dazu auf der Konsole des Hosts folgende Kommandos ausfhren:", "ru": "-        ioBroker    .          :", "pt": "Devido ao grande nmero de plataformas de hardware que o ioBroker pode ser executado, voc precisa atualizar o js-controller manualmente. Para fazer isso, execute os seguintes comandos no console do host:", "nl": "Vanwege het veelvoud aan Platformen waarop ioBroker beschikbaar is kan een systeemupdate alleen handmatig uitgevoerd worden. Ga voor handmatige update naar je systeem via de console en voer het volgende uit:", "fr": "En raison des plates-formes trs diffrentes, o ioBroker peut fonctionner, uniquement la mise  jour manuelle est possible. Pour excuter la mise  jour manuelle, veuillez excuter les instructions suivantes via la console de l'hte:", "it": "Poich ioBroker funziona su molte piattaforme molto diverse, al momento  possibile solo l'aggiornamento manuale. Per avviare l'aggiornamento manuale, andare al controller tramite console ed eseguire quanto segue:", "es": "Debido al gran nmero de plataformas de hardware que el ioBroker se puede ejecutar, es necesario actualizar el JS-Controller manualmente. Para ello, ejecute los siguientes comandos en la consola de host:", "pl": "Poniewa ioBroker dziaa na wielu bardzo rnych platformach, w tej chwili moliwe jest tylko rczne aktualizowanie. Aby rozpocz aktualizacj rczn, przejd do kontrolera za pomoc konsoli i wykonaj nastpujce czynnoci:"},
